  display: flex;
  cursor: pointer;
  outline: none;
  flex-wrap: wrap;
}

.upload.is-card {
  padding: 1rem;
  border: dashed 1px #dee2e6;
  border-radius: 6px;
}

.upload.is-disabled {
  cursor: not-allowed;
}

.upload-input {
  display: none;
}

.upload-tip {
  margin-top: 0.5rem;
}

.upload-prev {
  display: inline-flex;
  flex-wrap: wrap;
  border: 1px dashed #dee2e6;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.upload-prev.is-invalid {
  border-style: solid;
}

.upload-prev.is-valid {
  border: solid 1px #4da3ff;
}

.upload-prev:hover {
  border-color: #4da3ff;
}

.upload-prev:not(:first-child) {
  margin-left: 1rem;
}

.upload-prev .fa-plus,
.upload-prev .fa-spinner {
  font-size: 1rem;
  position: absolute;
}

.upload-prev img {
  width: 100%;
  height: 100%;
  display: none;
}

.is-disabled .upload-prev {
  cursor: not-allowed;
  color: #dee2e6;
}

.is-card .upload-prev {
  padding: 1rem;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  border-style: solid;
  align-items: flex-start;
}

.is-card .upload-prev img {
  width: calc(100% - 2rem);
  height: auto;
  max-height: calc(100% - 4rem);
  border-radius: 4px;
}

.is-card .upload-prev .fa-plus,
.is-card .upload-prev .fa-spinner {
  top: 50%;
}

.is-card .upload-prev {
  margin: 0 1rem 1rem 0;
}

.upload-prev-info {
  width: 100%;
  align-self: flex-end;
  position: relative;
}

.upload-prev-info .upload-list-item-size {
  text-align: center;
  font-size: 11px;
  line-height: 2;
  margin-bottom: 0.5rem;
}

.upload-footer-toolbar {
  display: flex;
  justify-content: space-between;
}

.upload-prev-progress {
  position: absolute;
  background-color: #f3f3f3;
  display: none;
  bottom: 20px;
  border-radius: 3px;
  left: 1rem;
  right: 1rem;
}

.is-invalid .upload-prev-progress .upload-prev-progress-cur {
  background-color: #dc3545;
}

.is-invalid.upload-prev {
  border-color: #dc3545;
}

.upload-item-circle {
  transform: rotate(-90deg);
  position: absolute;
}

.upload-item-circle-progress {
  stroke: #007bff;
}

.is-valid .upload-item-circle-progress {
  stroke: #4da3ff;
}

.is-invalid .upload-item-circle-progress {
  stroke: #dc3545;
}

.upload-prev-progress-text {
  font-size: 10px;
  line-height: 10px;
  position: absolute;
  bottom: 12px;
}

.upload-prev-progress-cur {
  height: 6px;
  width: 0;
  border-radius: 3px;
  transition: width 0.3s linear;
}

.upload-button {
  align-self: flex-end;
  margin-left: 1rem;
}

.upload-item-label {
  position: absolute;
  top: -2px;
  right: -14px;
  text-align: center;
  transform: rotate(45deg);
  color: #fff;
  width: 46px;
  height: 20px;
  display: none;
}

.upload-item-label .fa-check {
  transform: rotate(-45deg);
  font-size: 12px;
}

.upload-item-actions {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;
  align-items: center;
  justify-content: center;
}

.upload-item-actions .upload-item-delete {
  cursor: pointer;
}

.upload-prev.is-valid img,
.is-valid .upload-item-label,
.is-card .upload-prev.is-valid:hover .upload-item-label,
.is-card .is-valid .upload-prev-info,
.is-progress .is-uploading .upload-prev-progress,
.is-load img,
.upload-prev.is-active .fa-spinner {
  display: block;
}

.upload-prev:hover .upload-item-label,
.is-circle .is-valid .upload-item-label,
.is-active .fa-plus,
.is-load .fa-plus,
.is-load .fa-spinner,
.is-valid .fa-plus,
.is-card .upload-prev.is-valid:hover .upload-item-actions,
.is-stack .upload-prev,
.upload-prev-info,
.is-progress .is-valid .upload-prev-progress,
.upload-prev .fa-spinner {
  display: none;
}

.is-circle .upload-prev,
.is-circle .upload-prev img {
  border-radius: 50%;
}

.is-circle .upload-prev .upload-item-label {
  width: 100%;
  bottom: -8px;
  top: unset;
  left: 0;
  transform: rotate(0);
  height: 16px;
  line-height: 16px;
}

.is-circle .upload-prev .upload-item-label .fa-check {
  transform: rotate(0);
}

.upload-prev.is-valid:hover .upload-item-actions {
  display: flex;
  opacity: 1;
}

.upload-list {
  margin: 0;
  padding: 0;
  list-style: none;
  min-width: 250px;
  max-height: 200px;
  overflow: auto;
}

.upload-list-item:first-child {
  margin-top: 10px;
}

.upload-list-item {
  font-size: 14px;
  color: #868e96;
  line-height: 1.8;
  margin-top: 0.5rem;
  border-radius: 4px;
  width: 100%;
  display: flex;
  position: relative;
}

.upload-prev-progress-info {
  display: none;
}

.upload-prev-progress-info .upload-list-item-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.upload-list-item-rate {
  display: none;
}

.upload-body {
  display: flex;
  flex-wrap: wrap;
}

.upload-file-icon {
  font-size: 60px;
  line-height: 60px;
  display: none;
}

.is-card .upload-file-icon {
  font-size: 80px;
  line-height: 80px;
  margin-top: 40px;
}

.is-stack .upload-file-icon {
  width: 100px;
  text-align: center;
  border: solid 1px #dee2e6;
  border-radius: 4px;
  margin: 4px;
  padding: 4px;
}

.is-stack {
  flex-direction: column;
}

.is-stack .upload-button {
  align-self: flex-start;
  margin-left: 0;
}

.is-stack .upload-prev {
  margin-left: 0;
  height: 80px;
  justify-content: flex-start;
}

.is-stack .upload-prev:not(:nth-child(2)) {
  margin-top: 1rem;
}

.is-stack .upload-prev img {
  width: 100px;
  height: auto;
  max-height: 70px;
  border-radius: 4px;
  border: solid 1px #dee2e6;
  margin: 0.25rem;
}

.is-stack .fa-plus,
.is-stack .upload-prev .fa-spinner {
  display: none;
}

.is-stack.is-progress .upload-prev-progress {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  left: 130px;
  right: 50px;
}

.is-stack .upload-prev-progress .upload-prev-progress-text {
  bottom: 0;
  right: 0.5rem;
  color: #000;
}

.is-stack .upload-prev-progress .upload-prev-progress-cur {
  height: 10px;
}

.is-stack .upload-prev-progress-info {
  display: flex;
  position: absolute;
  bottom: 1.25rem;
  left: 0;
  right: 0;
}

.is-stack .upload-list-item-rate {
  display: block;
}

.is-stack .upload-body {
  overflow: auto;
  max-height: 384px;
  padding-right: 1rem;
  margin-top: 1rem;
}

.is-stack .upload-prev .upload-item-actions {
  position: absolute;
  bottom: 1rem;
  right: 0.75rem;
  left: auto;
  top: auto;
  width: auto;
  height: auto;
  background: none;
  opacity: 1;
  font-size: 14px;
  border: solid 1px #dc3545;
  border-radius: 0.2rem;
  padding: 0.0625rem 0.3125rem;
}

.is-stack .upload-prev .upload-item-actions .upload-item-delete {
  color: #dc3545;
}

.is-stack .upload-prev .upload-item-actions:hover {
  background-color: #dc3545;
  border-color: #dc3545;
}

.is-stack .upload-prev .upload-item-actions:hover .upload-item-delete {
  color: #fff;
}

.is-stack .upload-prev:hover .upload-item-actions {
  display: block;
}

.is-stack .is-invalid-file .upload-prev-progress {
  display: none;
}

.upload-prev-invalid-file {
  display: none;
}

.is-stack .is-invalid-file .upload-prev-invalid-file {
  display: flex;
  width: 100%;
  margin: 0 4rem 0 1rem;
}

.upload-prev-invalid-file .file-name {
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 180px;
  white-space: nowrap;
}

.is-file.upload-prev img {
  display: none;
}

.is-file .upload-file-icon {
  display: block;
}

.upload .btn-reset {
  margin-left: 0.5rem;
}

/*end upload*/
/*divider*/
.divider {
  background-color: #dee2e6;
  position: relative;
}

.divider .divider-text {
  position: absolute;
  background-color: #fff;
  padding: 0 20px;
  font-weight: 500;
  color: #939ba2;
  font-size: 14px;
}

.divider.divider-horizontal {
  display: block;
  height: 1px;
  width: 100%;
  margin: 1rem 0;
}

.divider.divider-horizontal .divider-text.is-left {
  left: 20px;
  transform: translateY(-50%);
}

.divider.divider-horizontal .divider-text.is-center {
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.divider.divider-horizontal .divider-text.is-right {
  right: 20px;
  transform: translateY(-50%);
}

.divider-vertical {
  width: 1px;
  margin: 0 1rem;
}

.divider.divider-vertical .divider-text {
  padding: 20px 0;
}

.divider.divider-vertical .divider-text.is-left {
  top: 20px;
  transform: translateX(-50%);
}

.divider.divider-vertical .divider-text.is-center {
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.divider.divider-vertical .divider-text.is-right {
  bottom: 20px;
  transform: translateX(-50%);
}

/*end divider*/
/*accordion*/
.accordion .card-header {
  padding: 6px;
}

.accordion .card-header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 0.5rem;
}

.accordion .card-header-wrapper .fa-angle-down {
  transition: transform 0.3s linear;
}

.accordion .card-header-wrapper .collapsed + .fa-angle-down {
  transform: rotate(-90deg);
}

.accordion .collapse-item {
  font-size: 13px;
  line-height: 1.8;
}

/*end accordion*/
/*avatar*/
.avatar {
  border-radius: 4px;
  width: 50px;
  height: 50px;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #f3f3f3;
  color: #fff;
}

.avatar img {
  width: 100%;
  object-fit: cover;
}

.avatar.is-circle {
  border-radius: 50%;
}

.avatar.is-xl {
  width: 70px;
  height: 70px;
}

.avatar.is-lg {
  width: 60px;
  height: 60px;
}

.avatar.is-sm {
  width: 40px;
  height: 40px;
}

.avatar.is-xs {
  width: 30px;
  height: 30px;
}

.avatar i {
  font-size: 1.4rem;
}

/*end avatar*/
/*tag*/
.tag {
  display: inline-block;
  height: 32px;
  padding: 0 10px;
  line-height: 30px;
  font-size: 12px;
  border-radius: 4px;
  white-space: nowrap;
  margin: 0;
}

.tag.is-close {
  padding-right: 30px;
}

.tag.is-close .close {
  top: 8px;
}

.tag > i {
  margin-right: 2px;
}

/*end tag*/
/*rate*/
.rate {
  height: 18px;
  line-height: 1;
  font-size: 18px;
  cursor: pointer;
}

.rate.disabled {
  cursor: not-allowed;
}

.rate-item {
  width: 18px;
  height: 100%;
  margin-right: 6px;
}

.rate-item:last-child {
  margin-right: 0;
}

.rate-item i {
  color: #80bdff;
  transition: 0.3s;
}

.rate-item.is-on i {
  color: #80bdff;
}

.rate-item.is-on i:before {
  content: "\f005";
}

.rate:not(.disabled) .rate-item.is-on i {
  color: #ffc107;
}

/*end rate*/
/*timeline*/
.timeline {
  margin: 0;
  padding: 0;
  list-style: none;
}

.timeline .time-line-item {
  position: relative;
  padding-bottom: 1rem;
}

.timeline .time-line-item:last-child {
  padding-bottom: 0;
}

.timeline .time-line-item .time-line-item-tail {
  position: absolute;
  left: 4px;
  height: 100%;
  border-left: 2px solid #dee2e6;
}

.timeline .time-line-item:last-child .time-line-item-tail {
  display: none;
}

.timeline .timeline-item-node-normal {
  left: -1px;
  width: 12px;
  height: 12px;
}

.timeline .timeline-item-node {
  position: absolute;
  background-color: #dee2e6;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeline .timeline-item-node.is-icon {
  background-color: transparent;
}

.timeline .timeline-item-wrapper {
  position: relative;
  padding-left: 1.5rem;
  top: -4px;
}

.timeline .timeline-item-content {
  color: #939ba2;
}

.timeline .timeline-item-timestamp {
  margin-top: 0.5rem;
  color: #868e96;
}

.timeline.is-alternate .time-line-item-tail,
.timeline.is-alternate .timeline-item-node {
  left: 50%;
}

.timeline.is-alternate .timeline-item-node {
  margin-left: -5px;
}

.timeline.is-alternate .time-line-item:nth-child(odd) .timeline-item-wrapper {
  left: calc(50% - 5px);
  width: calc(50% - 1rem);
  text-align: left;
}

.timeline.is-alternate .time-line-item:nth-child(even) .timeline-item-wrapper {
  width: calc(50% - 1rem);
  margin: 0;
  text-align: right;
}

.timeline.is-left .time-line-item-tail,
.timeline.is-left .timeline-item-node {
  left: 100%;
}

.timeline.is-left .timeline-item-node {
  margin-left: -5px;
}

.timeline.is-left .time-line-item .timeline-item-wrapper {
  width: calc(100% - 1rem);
  margin: 0;
  text-align: right;
}

/*end timeline*/
/*footer*/
.footer {
  background: #868e96;
  color: #fff;
  padding: 10px 4px;
  height: 40px;
  display: flex;
}

.footer .footer-text {
  flex: 1 1 auto;
  text-align: center;
}

/*end footer*/
/*menu*/
.menu {
  border-bottom: 1px solid #dee2e6;
}

.menu .card,
.menu .card .card-header {
  background-color: inherit;
}

.menu .navbar {
  padding: 0 1rem;
}

.menu .nav-link {
  position: relative;
}

.menu .nav-link i + span,
.menu .dropdown-item i + span,
.menu .nav-link .badge {
  margin-left: 4px;
}

.menu .navbar-nav > .nav-item {
  border-bottom: solid 2px transparent;
  position: relative;
}

.menu .navbar-nav > .nav-item.active {
  color: #007bff;
}

.menu .navbar-nav > .nav-item:after {
  content: "";
  position: absolute;
  background-color: #007bff;
  left: 50%;
  height: 2px;
  width: 0px;
  transition: width 0.3s linear, left 0.3s linear;
}

.menu .navbar-nav > .nav-item.active:after {
  width: 100%;
  left: 0;
}

.menu .nav-item .nav-link {
  padding: 0.5rem 1rem;
  color: rgba(0, 0, 0, 0.65);
  transition: color 0.3s linear;
  user-select: none;
  white-space: nowrap;
}

.menu .nav-item .nav-link.active {
  color: #007bff;
}

.menu .nav-item .nav-link:hover {
  color: #007bff;
}

.menu .dropdown-menu {
  overflow: unset;
  margin-top: 10px;
}

.menu .dropdown-menu .dropdown-item {
  position: relative;
}

.menu .dropdown-menu .dropdown-item,
.menu .sub-menu .nav-item {
  padding: 6px 20px;
  transition: background-color 0.3s linear;
}

.menu .dropdown-menu .dropdown-item:hover,
.menu .dropdown-menu .dropdown-item.active,
.menu .sub-menu .nav-item.active,
.menu .sub-menu .nav-item:hover {
  background-color: #4da3ff;
}

.menu .dropdown-menu-arrow {
  width: 0;
  height: 0;
  border-width: 0 6px 6px;
  border-style: solid;
  border-color: transparent transparent rgba(0, 0, 0, 0.15);
  position: absolute;
  left: 16px;
  margin-top: -15px;
}

.menu .dropdown-menu-arrow:after {
  content: " ";
  width: 0;
  height: 0;
  border-width: 0 6px 6px;
  border-style: solid;
  border-color: transparent transparent #fff;
  position: absolute;
  top: 1px;
  left: -6px;
}

.menu .sub-menu {
  display: none;
  background-color: #fff;
  white-space: nowrap;
  border: solid 1px #dee2e6;
  border-radius: 4px;
  padding: 0.5rem 0;
  position: absolute;
  left: 100%;
  top: -9px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.menu .sub-menu .nav-item {
  border-bottom: none;
  min-width: 10rem;
}

.menu .sub-menu .nav-item .nav-link {
  padding: 0;
  color: #212529;
  position: relative;
}

.menu .dropdown-item:hover > .sub-menu,
.menu .nav-item:hover > .nav-link > .sub-menu {
  display: block;
}

.menu .nav-item:hover > .nav-link > .sub-menu {
  left: calc(100% + 20px);
  top: -15px;
}

.menu .has-leaf > .nav-link-right {
  transition: transform 0.3s linear;
  position: absolute;
  right: 14px;
  top: 9px;
}

.menu .sub-menu .has-leaf > .nav-link-right {
  right: 0;
  top: 3px;
}

.menu .has-leaf:hover > .nav-link-right {
  transform: rotate(-90deg);
}

.menu.is-vertical {
  border: none;
  width: 100%;
}

.menu.is-vertical .accordion .card {
  border: none;
}

.menu.is-vertical .accordion .card-header {
  padding: 0;
  margin: 0;
  border: none;
}

.menu.is-vertical .accordion .card-header-wrapper {
  padding-right: 0.75rem;
}

.menu.is-vertical .accordion .nav-link {
  flex: 1 1 auto;
  user-select: none;
}

.menu.is-vertical .nav-link {
  transition: color 0.3s linear;
  color: inherit;
  white-space: nowrap;
}

.menu.is-vertical .nav-link.active,
.menu.is-vertical .nav-link:hover,
.menu.is-vertical .nav-link.active + i,
.menu.is-vertical .nav-link:hover + i {
  color: #4da3ff;
}

.menu.is-vertical .accordion .card-body {
  padding: 0;
}

.menu.is-vertical .has-leaf > .accordion {
  padding-left: 20px;
}

/* side menu*/
.menu.is-vertical .nav-link .nav-link-text {
  transition: width 0.3s linear, opacity 0.3s linear;
  opacity: 1;
}

.menu.is-vertical .has-leaf > .accordion {
  transition: padding-left 0.3s linear;
}

.menu .nav-link-text {
  display: inline-block;
}

.menu .nav-item.disabled {
  cursor: not-allowed;
}

.menu .nav-item.disabled .nav-link,
.menu .nav-link.disabled,
.menu .card-header-wrapper.disabled,
.dropdown-item.disabled {
  cursor: not-allowed;
  color: #dee2e6;
}

@media (min-width: 768px) {
  .menu.is-vertical.is-collapsed .nav-link .nav-link-text {
    width: 0;
    opacity: 0;
  }
  .menu.is-vertical.is-collapsed .has-leaf > .accordion {
    padding-left: 0;
  }
}

/*end menu*/
/*layout*/
.layout {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
}

.layout .layout-main {
  flex: 1;
}

.has-sidebar {
  flex-direction: row;
  display: flex;
  flex: 1;
}

.layout-side {
  position: relative;
}

.layout-gotop {
  background: rgba(255, 255, 255, 0.5);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 4px;
  transition: background-color 0.3s linear;
  color: #868e96;
}

.layout-gotop:hover {
  background-color: #fff;
  color: #868e96;
}

.layout.is-page .layout-side {
  transform: translateX(-100%);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: auto;
  transition: transform 0.3s linear;
  z-index: 1030;
}

.layout.is-page .layout-side.is-collapsed {
  transform: translateX(0);
}

.layout.is-page .has-sidebar .layout-side {
  top: 50px;
}

.layout.is-page .layout-right,
.layout.is-page .has-sidebar .layout-main {
  width: 100%;
}

.layout.is-page.is-fixed {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.layout.is-page .layout-header {
  background-color: #17a2b8;
  height: 50px;
  color: #fff;
  display: flex;
  align-items: center;
  padding: 0 1rem;
}

.layout.is-page .layout-side {
  background-color: #117a8b;
  color: #dee2e6;
}

.layout-header-bar {
  padding: 4px 12px;
  color: #fff;
  background-color: #48d368;
  border-color: #48d368;
