/*step*/
.steps {
  display: flex;
}

.steps-horizontal {
  white-space: nowrap;
}

.step {
  position: relative;
  flex-shrink: 1;
}

.step.is-horizontal {
  display: inline-block;
}

.step.is-center .step-head,
.step.is-center .step-main {
  text-align: center;
}

.step-head {
  position: relative;
  width: 100%;
  line-height: 1;
}

.step-head.is-success {
  color: #48d368;
  border-color: #48d368;
}

.step-head.is-process {
  color: #939ba2;
  border-color: #939ba2;
}

.step-head.is-wait {
  color: #f3f3f3;
  border-color: #f3f3f3;
}

.step-head.is-finish {
  color: #4da3ff;
  border-color: #4da3ff;
}

.step-head.is-error {
  color: #e77681;
  border-color: #e77681;
}

.step-main {
  white-space: normal;
  text-align: left;
}

.step-title {
  font-size: 14px;
  line-height: 38px;
}

.step-title.is-success {
  color: #48d368;
}

.step-title.is-finish {
  color: #4da3ff;
}

.step-title.is-process {
  font-weight: 700;
  color: #939ba2;
}

.step-title.is-wait {
  color: #f3f3f3;
}

.step-title.is-error {
  color: #e77681;
}

.step-description {
  padding-right: 10%;
  margin-top: -5px;
  font-size: 12px;
  line-height: 20px;
  font-weight: 400;
}

.step-description.is-success {
  color: #48d368;
}

.step-description.is-finish {
  color: #4da3ff;
}

.step-description.is-process {
  color: #939ba2;
}

.step-description.is-wait {
  color: #f3f3f3;
}

.step.is-center .step-description {
  padding-left: 20%;
  padding-right: 20%;
}

.step-line {
  position: absolute;
  border-color: inherit;
  background-color: #f3f3f3;
}

.step.is-horizontal .step-line {
  height: 2px;
  top: 11px;
  left: 0;
  right: 0;
}

.step.is-center .step-line {
  left: 50%;
  right: -50%;
}

.step-line-inner {
  display: block;
  border: 1px solid;
  border-color: inherit;
  transition: 0.15s ease-out;
  box-sizing: border-box;
  width: 0;
  height: 0;
}

.step:last-of-type .step-line {
  display: none;
}

.step-icon {
  position: relative;
  z-index: 1;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  font-size: 14px;
  box-sizing: border-box;
  background: #fff;
  transition: 0.15s ease-out;
}

.step-icon.is-text {
  border-radius: 50%;
