/*drawer*/
.drawer-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transform: translateX(-100%);
  z-index: 2020;
}

.drawer-wrapper.is-open {
  transform: translateX(0);
}

.drawer-container {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}

.drawer {
  position: absolute;
  box-sizing: border-box;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
  overflow: hidden;
  transition: transform 0.3s linear;
  transform: translateX(-100%);
}

.drawer.left,
.drawer.right {
  height: 100%;
  top: 0;
  bottom: 0;
}

.drawer.top,
.drawer.bottom {
  width: 100%;
  left: 0;
  right: 0;
}

.drawer.right {
  right: 0;
  transform: translateX(100%);
}

.drawer.top {
  top: 0;
  transform: translateY(-100%);
}

.drawer.bottom {
  bottom: 0;
  transform: translateY(100%);
}

.is-open .drawer {
  transform: translateX(0);
}

