  opacity: 0;
  top: -20px;
  bottom: unset;
  margin: 1rem auto 0 auto;
  transition: opacity 0.3s linear, top 0.3s linear, bottom 0.3s linear;
  pointer-events: auto;
}

.message .alert.show {
  opacity: 1;
  bottom: unset;
  top: 20px;
}

.message.is-bottom .alert {
  top: unset;
  bottom: -20px;
}

.message.is-bottom .alert.show {
  top: unset;
  bottom: 20px;
}

